import React, { Component, Suspense } from 'react';
import { connect } from "react-redux";
import Dropdown from 'react-bootstrap/Dropdown';
import { Link } from 'react-router-dom';
import { baseUrl } from '../../../Assets/AssetsHelper';
import SVG from 'react-inlinesvg';
import { MEDIA_REPLACE_URL } from '../../../Services/Utils/utils'
import { logOutUser } from '../../../Services/Actions/loginActions'
import { uploadModalClick } from '../../../Services/Actions/homeActions'
import ProfileImage from '../../Main/CommonComponents/ProfileImage/ProfileImage';
import { COPY_TO_CLIPBOARD } from '../../../Services/Utils/utils';
import $ from 'jquery'

const AddAssetsModal = React.lazy(() => import('../../Main/MyAssets/AddAssetsModal/AddAssetsModal'));
const Notifications = React.lazy(() => import('./Elements/Notifications'));

class HeaderManu extends Component {
    constructor(props) {
        super(props);
        this.state = { isActiveAside: false, appThemeSkin: props.cookies ? props.cookies : 'light', editMediaShowModal: false, logOutSpinner: false };
        if (props.cookies) {
            const htmlTag = document.getElementsByTagName("html")[0];
            htmlTag.setAttribute("data-theme", props.cookies);
        }
    }
    setMode = (ele) => {
        const htmlTag = document.getElementsByTagName("html")[0];
        htmlTag.setAttribute("data-theme", ele);
        this.props.uploadModalClick(this.state.editMediaShowModal)
        this.setState({ appThemeSkin: ele }, () => this.props.setAppThemeSkin(ele));

    };

    editMediaHandleModalShow = () => {
        this.setState({
            editMediaShowModal: true
        })
    }

    editMediaHandleCallback = (editMediaEvent) => {

        this.setState({
            editMediaShowModal: editMediaEvent
        })
        this.props.uploadModalClick(false)
    }
    onClickSignOut = event => {
        this.setState({
            logOutSpinner: true
        })
        this.props.logOutUser()
    }
    componentWillReceiveProps(nextProps) {
        if (nextProps.uploadStatus != this.state.editMediaShowModal) {
            const { uploadStatus } = nextProps
            if (uploadStatus) this.setState({ editMediaShowModal: true })
            else {
                this.setState({ editMediaShowModal: false })
                if ($("#resetUppyUpload") && $(".uppy-Container").length > 0){
                    $("#resetUppyUpload").click()
                }
            }
        }
    }
    onEmailCopy = (id) => event => COPY_TO_CLIPBOARD(id)
    render() {
        const { appThemeSkin, editMediaShowModal, logOutSpinner } = this.state;
        const { userData, notificationList, history } = this.props
        return (
            <div className="header-menu d-flex align-items-center justify-content-end w-xs-100 h-100">
                <div className="d-flex align-items-center h-100 flex-shrink-0">
                    <Dropdown id="head_skin_" align="end" className="block-center position-relative head_skin_">
                        <Dropdown.Toggle variant="link" className="btn btn-icon btn-active-light btn-active-color-primary p-0 text-decoration-none transition-none arrow_disabled">
                            {appThemeSkin === 'light' ?
                                <i className="ri-sun-fill icon-6"></i>
                                : null}
                            {appThemeSkin === 'dark' ?
                                <i className="ri-moon-clear-fill icon-6"></i>
                                : null}
                            {appThemeSkin === 'system' ?
                                <span className="svg-icon me-0 text-dark">
                                    <SVG src={baseUrl('media/icons/svg-icons/computer-setting.svg')} height={16} width={16} />
                                </span>
                                : null}
                        </Dropdown.Toggle>

                        <Dropdown.Menu>
                            <div className="menu-item">
                                <Dropdown.Item className={`${appThemeSkin === 'light' ? 'active ' : ''}px-3 py-2`} onClick={() => this.setMode("light")}>
                                    <i className="ri-sun-fill icon-6 me-2 text-dark"></i>
                                    <span className="menu-title">Light</span>
                                </Dropdown.Item>
                            </div>
                            <div className="menu-item">
                                <Dropdown.Item className={`${appThemeSkin === 'dark' ? 'active ' : ''}px-3 py-2`} onClick={() => this.setMode("dark")}>
                                    <i className="ri-moon-clear-fill icon-6 me-2 text-dark"></i>
                                    <span className="menu-title">Dark</span>
                                </Dropdown.Item>
                            </div>
                            <div className="menu-item">
                                <Dropdown.Item className={`${appThemeSkin === 'system' ? 'active ' : ''}px-3 py-2`} onClick={() => this.setMode("system")}>
                                    <span className="svg-icon svg-icon-5">
                                        <SVG src={baseUrl('media/icons/svg-icons/computer-setting.svg')} height={16} width={16} />
                                    </span>
                                    <span className="menu-title">System</span>
                                </Dropdown.Item>
                            </div>
                        </Dropdown.Menu>
                    </Dropdown>

                    <div className="head_upload_ block-center border-end">
                        <button type="button" className={`btn btn-primary d-flex align-items-center ${localStorage.getItem("expired") ? `disabled` : ``}`} onClick={this.editMediaHandleModalShow}>
                            <span className="svg-icon svg-icon-5 me-lg-2 me-0">
                                <SVG src={baseUrl('media/icons/svg-icons/cloud-upload.svg')} />
                            </span>
                            <span className="d-lg-flex d-none fw-medium fs-8">Upload</span>
                        </button>
                    </div>
                    <Dropdown className="block-center head_notifications_" align="end">
                        <Dropdown.Toggle variant="link" id="head_notifications_" className="btn btn-icon btn-active-light btn-active-color-primary text-decoration-none p-0 position-relative arrow_disabled">
                            <span className="d-flex align-items-center justify-content-center">
                                <span className="notifications_count bg-primary text-light-alt position-absolute rounded-circle" id="notificationCount" style={{ display: 'none' }} > 0</span>
                                <i className="ri-notification-4-line icon-4"></i>
                            </span>
                        </Dropdown.Toggle>

                        <Dropdown.Menu className="p-0">
                            <Suspense fallback={<></>}>
                                <Notifications notificationList={notificationList} />
                            </Suspense>
                        </Dropdown.Menu>
                    </Dropdown>


                    <Dropdown className="block-center user_profile_" align="end" autoClose={true}>
                        <Dropdown.Toggle variant="link" id="user_profile_" className="btn author_profile p-0 border-0 text-decoration-none d-block rounded-circle overflow-hidden arrow_disabled">
                            {userData && Object.keys(userData).length > 0 ? <ProfileImage className={'symbol symbol-38 rounded-circle bg-light'}
                                imageSize={38}
                                symbolClass={'symbol-label text-light-alt fw-bolder text-uppercase fs-5'}
                                name={userData.user.name} profileUrl={MEDIA_REPLACE_URL(userData.user.picture)} networkId={0} /> : null}
                        </Dropdown.Toggle>

                        {userData && Object.keys(userData).length > 0 ? <Dropdown.Menu className="p-0">
                            <div className="d-flex align-items-center justify-content-between w-100 p-3 border-bottom">
                                <div className="d-flex align-items-center me-2">
                                    <Link to={"/profile"} className="text-decoration-none">
                                        <ProfileImage className={'symbol symbol-40 rounded-circle flex-shrink-0 me-2 overflow-hidden bg-light'}
                                            imageSize={40}
                                            symbolClass={'symbol-label text-light-alt fw-bolder text-uppercase fs-5'}
                                            name={userData.user.name} profileUrl={MEDIA_REPLACE_URL(userData.user.picture)} networkId={0} />
                                    </Link>
                                    <div className="">
                                        <p className="text-dark mb-0">
                                            <Link to={"/profile"} className="text-decoration-none"><strong className="fs-6 fw-bold text-break text-gray-800">{userData.user.name}</strong></Link>
                                        </p>
                                        <span className="mb-0 text-gray-600 text-break fs-8 cursor-pointer" id="headerProfileEmail" onClick={this.onEmailCopy("headerProfileEmail")}>{userData.user.emailId}</span>
                                    </div>
                                </div>
                            </div>
                            <div className="px-5 mb-3">
                                <Dropdown.Item className="navi-item px-0 py-2 py-xxl-3 border-bottom dropdown-item rounded-0" onClick={event => history("/profile")}>
                                    <div className="symbol symbol-40 bg-light me-3">
                                        <div className="symbol-label text-dark">
                                            <i className="ri-user-3-line icon-6 me-0"></i>
                                        </div>
                                    </div>
                                    <div className="navi-text">
                                        <div className="fs-7 fw-semibold mb-1">My Profile</div>
                                        <div className="text-gray-600 fs-8">View/Edit your personal information </div>
                                    </div>
                                </Dropdown.Item>
                                <Dropdown.Item className="navi-item px-0 py-2 py-xxl-3 border-bottom dropdown-item rounded-0" onClick={event => history("/billing")}>
                                    <div className="symbol symbol-40 bg-light me-3">
                                        <div className="symbol-label text-dark">
                                            <i className="ri-contacts-line icon-6 me-0"></i>
                                        </div>
                                    </div>
                                    <div className="navi-text">
                                        <div className="fs-7 fw-semibold mb-1">My Account</div>
                                        <div className="text-gray-600 fs-8">Account information and plan details</div>
                                    </div>
                                </Dropdown.Item>
                                <button type="button" onClick={this.onClickSignOut} className={`navi-item px-0 py-2 py-xxl-3 dropdown-item rounded-0 ${logOutSpinner ? 'disabled' : ''}`}>
                                    <div className="symbol symbol-40 bg-light me-3">
                                        <div className={`symbol-label ${logOutSpinner ? 'text-gray-500' : 'text-dark'}`}>
                                            <i className="ri-logout-circle-r-line icon-6 me-0"></i>
                                        </div>
                                    </div>
                                    <div className="navi-text">
                                        <div className={`fs-7 fw-semibold mb-1 ${logOutSpinner ? 'spinner spinner-gray text-gray-500' : ''}`}>Sign Out</div>
                                    </div>
                                </button>
                            </div>
                        </Dropdown.Menu> : null}
                    </Dropdown>
                </div>
                <Suspense fallback={<></>}>
                    {editMediaShowModal ? <AddAssetsModal editMediaHandleCallback={this.editMediaHandleCallback} editMediaToggle={editMediaShowModal} {...this.props} /> : null}
                </Suspense>
            </div>
        );
    }
}


const mapStateToProps = (state) => {

    return {
        userData: state.authenticated.userData,
        uploadStatus: state.modalstatus.uploadStatus,
        notificationList: state.notifications.notificationList
    }
}

const mapDispatchToProps = dispatch => {
    return {
        logOutUser: () => dispatch(logOutUser()),
        uploadModalClick: (status) => dispatch(uploadModalClick(status))

    }
}
export default connect(mapStateToProps, mapDispatchToProps)(HeaderManu);